.container {
  width: 100%;

  padding-left: 15px;
  padding-right: 15px;
  height: auto;
  margin: 0 auto;

  @media screen and (min-width: 480px) {
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1360px) {
    width: 1360px;
  }
}
